    }
}
@media (max-width: 1200px) {
    .context-banner { 
        padding-bottom: 150px;
        &__decor-wrapper {
            top: 18.5vw;
            width: 63vw;
            height: 49vw;
        }
        &__logo {
            margin-bottom: 60px;
        }
        &__subtitle {
            font-size: 64px;
        }
        &__link {
            padding: 0px 50px;
        }
        &__row {

        }
    }
}
@media (max-width:1024px) {
    .context-banner__row{
        display: block;
    }
    .context-banner__kurs-info{
        margin: 0 auto;
        margin-top: vw(0);
        margin-bottom: vw(52);
        width: vw(275);

        height: vw(350);
    }
    .context-banner__subtitle{
        font-size: 9.6vw;
        margin-bottom: 4vw;
    }
    .context-banner__decor-wrapper{
        top:-50px;
    }
}

@media (max-width:768px) {
    .context-banner__play-text{
        font-size: 5vw;
    }

    .context-banner{
        padding-top: 25.86667vw;
        padding-bottom: 25.06667vw;
    }
    .context-banner__logo{
        width: 8.53333vw;
        height: 8.53333vw;
        margin-bottom: 25.06667vw;
    }
    .context-banner__link{
        font-size: 4.53333vw;
        padding: 0 13.33333vw;
        height: 15.46667vw;
        border-radius: 3.46667vw;
        min-width: 57.6vw;
        margin-left: auto;
        margin-right: auto;
    }
    .context-banner__decor-wrapper{
        top:-20vw;
        width: 100vw;
        height: 78vw;
        right: -17vw;
    }
}